#header {
  --temp_primary: var(--_secondary);
  --temp_dark: var(--_dark);
  --temp_light: var(--_light);
  width: 100%;
  height: 80px;
  color: var(--_light);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.header-scroll {
  background-color: var(--_dark);
  box-shadow: 0 2px 10px -2px var(--_light);
  --temp_primary: var(--temp_dark) !important;
  --temp_dark: var(--temp_light) !important;
}

#header .content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

#header .link {
  margin-left: 2vw;
  letter-spacing: 0.8px;
  cursor: pointer;
  position: relative;
  transition: all ease 0.5s;
  border: 5px solid transparent;
  position: relative;
}

#header .link::before {
  content: "";
  display: block;
  position: absolute;
  bottom: -12px;
  left: 0;
  height: 2px;
  background: var(--_secondary);
  border-radius: 2px;
  width: 100%;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  transition: all 0.4s ease-in;
}
#header .link:hover::before,
#header .link:focus {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

#header .active.link::before {
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
}

#header .active {
  box-shadow: 0 0 0 2px var(--_secondary);
  padding: 0.2rem 0.4rem;
  border-radius: 10px;
}
.join_me_btn {
