    min-height: 200px;
    border: 1px solid var(--_secondary);
    border-radius: 1rem;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}
.lan_inner_card {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  transition: transform 0.6s, -webkit-transform 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  border-radius: 1rem;
  box-shadow: 0 13px 30px rgb(0, 0, 0, 0.3) , 0 0px 15px rgb(0, 0, 0, 0.5);
}

.lan_inner_card .card_front {
  position: relative;
  background-color: var(--_primary);
  color: black;
}
.lan_inner_card .card_back {
  color: white;
  background-color: #2d3436;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.lang_outer_card:hover  .lan_inner_card{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  border-radius: 1rem;
}

.lang_outer_card:hover h1{
    -webkit-transform: translateZ(200px);
            transform: translateZ(200px);
}
.lan_inner_card .card {
  border: none;
  padding: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.lan_inner_card h1 {
  font-weight: 800;
  color: white;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  transition: all .5s ease;
  text-transform: uppercase;
  font-family: verdana;
  font-size: 2rem;
  margin-bottom: 1rem;
  text-shadow: 1px 1px 1px #919191, 1px 2px 1px #919191, 1px 3px 1px #919191,
    1px 4px 1px #919191, 1px 5px 1px #919191, 1px 6px 1px #919191,
    1px 7px 1px #919191, 1px 8px 1px #919191, 1px 9px 1px #919191,
    1px 10px 1px #919191, 1px 18px 6px rgba(16, 16, 16, 0.4),
    1px 22px 10px rgba(16, 16, 16, 0.2), 1px 25px 35px rgba(16, 16, 16, 0.2),
    1px 30px 60px rgba(16, 16, 16, 0.4);
}

@-webkit-keyframes card {
  0% {
    -webkit-transform: rotate(5deg) scale(1);
            transform: rotate(5deg) scale(1);
  }
  50% {
    -webkit-transform: rotate(0deg) scale(1.04);
            transform: rotate(0deg) scale(1.04);
  }
  100% {
    -webkit-transform: rotate(-5deg) scale(1);
            transform: rotate(-5deg) scale(1);
  }
}

@keyframes card {
  0% {
    -webkit-transform: rotate(5deg) scale(1);
            transform: rotate(5deg) scale(1);
  }
  50% {
    -webkit-transform: rotate(0deg) scale(1.04);
            transform: rotate(0deg) scale(1.04);
  }
  100% {
    -webkit-transform: rotate(-5deg) scale(1);
            transform: rotate(-5deg) scale(1);
  }
}
